@import "fonts.scss";
$color: #111;
h1,h2
{
    color: $color;
    font-family: 'Conv_MyriadPro-LightSemiExt';
    
}
.body{
   max-width:1920px;
   min-width:320px;
}
.main{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.left-side,
.right-side{
    display: flex; 
    flex-shrink: 1;  
    flex-direction: column;     
    min-width: 480px;
    align-items: space-between;
    background: lightgray; 
       
}

.left-side{
    flex-grow:1;
    width:44%;
    // max-width: 39vw;    
}
.right-side{
    flex-grow:1;
    margin-left: 2px;
    width:54%;
}

.photo-card{
    margin-top:10%;
    margin-left: 0%;
    margin-bottom: 3%;
    // padding-top: 10%;
    // padding-left: 0%;
    text-align: center;    

   .photo-card__image{
    margin-top: 5%;
    margin-left: auto;
    margin-right: auto;
    width: 16vw;
    height: 16vw;
    border: 1vw solid#fff;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0 0 0 1px #3d6cb0;
   }
   
   img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
   }
    h1,h2{
        text-transform: uppercase;
        margin:0px;
    }
    h1{
        font-size:4.3vw;//3.7em;//58px;//13.81pt;
        font-style: normal;
        margin-top: 6%;
        
    }
    h2{
        font-family: 'Conv_MyriadPro-Bold';
        font-size:4.9vw;//68px; //16.32pt;
        font-style:normal;
        font-weight: normal;
        margin-top: -3%;
        
    }
    span {        
        font-family: 'Conv_MyriadPro-Bold';
        font-size:1.9vw;//28px;// 6.61pt;
        text-transform: uppercase;
        font-style:normal;
        font-weight: normal;
        margin:0px;        
    }
}

.about-me{
    margin-top: 10%;
    margin-left: 10%;//12
    margin-right: 15%;
    display: flex;
    flex-wrap: wrap;
    flex-basis: 40%;
    max-width: 76%;
    .about-me__icon{
        align-self: center; 
        // background: url('../image/profile_icon.png') no-repeat;
        img{
            width: 100%;
            height: auto;
            object-fit: cover;
            width:auto;
            height: auto;
            overflow: hidden;
        }       
        
       
    }
    .about-me__header{        
        margin-left: 2%;
        align-self: center;
        
        span{          
            font-family: 'Conv_MyriadPro-Bold';
            color: #3d6cb0;
            font-size:3.4vw;//45px;
            text-transform: uppercase;
            font-style:normal;
            font-weight: normal;
        }
        
    }
    .about-me__line{
        flex-grow: 1;       
        margin-left: 4%;
        align-self: center;
        border-bottom: 5px solid #3d6cb0;
    }
    .about-me__text{
        p{
        font-family: 'Conv_MyriadPro-LightSemiExt';
        
            span{
                font-family: 'Conv_MyriadPro-Bold';
            }
        }
    }
    .contact{
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        flex-basis: 100%;
        margin-top: 4%;
        .contact__item{
          margin-top: 2%;
            .heading{       
                font-family: 'Conv_MyriadPro-Bold';
                text-transform: uppercase;
                font-style:normal;
                font-weight: normal;  
                font-size: 1.6vw;     
                color:#3d6cb0;
                display: block;
                float: left;
                margin-right:13%;
                width:80px;
                
            }
            .inner{
                font-family: 'Conv_MyriadPro-Bold';
                text-transform: uppercase;
                font-style:normal;
                font-weight: normal;
                font-size: 18px;//1.5vw;
                color:#111;
                display: block;

            }
        }
    }
    
}
